/* Webicons by Adam Fairhead at Fairhead Creative http://fairheadcreative.com */
.webicon {
  display: inline-block;
  /* IE fix */
  zoom: 1;
  *display: inline;
  /* /IE Fix */
  background-repeat: no-repeat;
  background-position: left center;
  min-width: 30px;
  height: 30px;
  line-height: 30px;
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  border-radius: 5px;
  margin-bottom: 5px; }

.webicon.small {
  min-width: 20px;
  height: 20px;
  line-height: 20px;
  padding-left: 24px;
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
  border-radius: 3px; }

.webicon.large {
  min-width: 48px;
  height: 48px;
  line-height: 48px;
  padding-left: 56px;
  -moz-border-radius: 6px;
  -webkit-border-radius: 6px;
  border-radius: 6px; }

